import { reqHome, reqTopTitle } from "@/api"

const state ={
    home:{},
    toptitle:[]
}
const mutations ={
    HOMEDATA(state, payload){
        state.home = payload
    },
    TOPTITLE(state, payload){
        state.toptitle = payload
    }
}
const actions ={
    async homeData({commit}){
        let result = await reqHome()
        if(result.code == 200){
            commit("HOMEDATA", result.data)
        }
    },
    async topTitle({commit}){
        let result = await reqTopTitle()
        if(result.code == 200){
            commit("TOPTITLE", result.data)
        }
    },

}
const getters ={
    banner(state){
        return state.home.banner || []
    },
    record(state){
        return state.home.record || [[]]
    },
    links(state){
        return state.home.links || []
    }
}

export default {
    
    state,
    mutations,
    actions,
    getters
}
  