import { createApp } from "vue";
import App from "./App.vue";
import router from "./router";
import store from "./store";
// import { ElImage,ElCarousel,ElAffix } from "element-plus";
// import 'element-plus/dist/index.css'
import './assets/reset.css'
import './mock/serve'
import  animate  from 'animate.css'
import {Swipe, SwipeItem,Image as VanImage, Icon,Sidebar, SidebarItem,  Collapse, CollapseItem, TreeSelect, Tab, Tabs, Card, Lazyload } from 'vant'
createApp(App)
        .use(Swipe)
        .use(SwipeItem)
        .use(VanImage)
        .use(Icon)
        .use(Sidebar)
        .use(SidebarItem)
        .use(Collapse)
        .use(CollapseItem)
        .use(TreeSelect)
        .use(Tab)
        .use(Tabs)
        .use(Card)
        .use(Lazyload)
        .use(animate)
        .use(store)
        .use(router)
        .mount("#app");
