<template>
  <div class="main-box">
    <div class="top">
      <img src="../assets/logo1.png" class="logo"/>

      <div class="title">
        <van-icon name="wap-nav" @click="isShow = !isShow" />
      </div>
      <div class="titieList animated bounceInRight" v-show="isShow" >
        <van-collapse v-model="activeNames" v-for="titleName in topTitle" :key="titleName.id" >
          <van-collapse-item :title="titleName.mingcheng" :name="titleName.id" class="titlelist" >
            <van-sidebar v-model="active" v-for="titleNameChild in titleName.child" :key="titleNameChild.id" >
              <van-sidebar-item :title="titleNameChild.mingcheng" :id="titleNameChild.id"/>
            </van-sidebar>
          </van-collapse-item>
        </van-collapse>
      </div>
    </div>
    <div class="banner">
      <van-swipe :autoplay="3000" lazy-render height="200">
        <van-swipe-item v-for="(bannerimg, index) in banner" :key="index">
          <van-image :src="bannerimg.path" class="bannerimg" />
        </van-swipe-item>
      </van-swipe>
    </div>
  </div>
</template>

<script> 
import { computed, reactive, ref, toRefs } from "vue";
import { useStore } from "vuex";
export default {
  name: "MyCarousel",
  setup() {
    const store = useStore();
    const isShow = ref(false);
    const active = ref(0);
    const activeNames = ref(["1"]);
    const activeIndex = ref(0);
    const banner = computed(() => {
      return store.getters.banner;
    });
    const topTitle = computed(() => {
      return store.state.home.toptitle
    });
    console.log(store.state.home.toptitle)

    return {
      banner,
      isShow,
      active,
      activeNames,
      topTitle,
      activeIndex,
    };
  },
};
</script>

<style lang="scss" scoped>
.top {
  padding: 10px 5px;
  height: 50px;
  background-image: url("../assets/title_bg.png");
  display: flex;
  .logo {
    flex: 4;
    width: 100%;
    height: 100%;
    padding-left: 10px;
  }

  .van-icon {
    color: rgb(233, 233, 233);
    font-size: 35px;
  }
  .title {
    text-align: center;
    flex: 1;
    line-height: 70px;
  }
  .titieList {
    position: fixed;
    right: 10px;
    top: 50px;
    z-index: 99;
  }

}
.banner {
  .bannerimg {
    width: 100%;
    height: 100%;
  }
}
.van-tree-select{
  width: 60vw;
}
</style>
